<template>
  <div id="table">
    <h1>{{ title }}</h1>
    <div class="container">
      <div class="row justify-content-center">
        <div class="col-12">
          <table>
            <thead>
              <tr>
                <th
                  @click="sort('name')"
                  :class="[sortBy === 'name' ? sortDirection : '']"
                >
                  Team
                </th>
                <th
                  @click="sort('Dominator')"
                  :class="[sortBy === 'Dominator' ? sortDirection : '']"
                >
                  Dominator
                </th>
                <th
                  @click="sort('Slaughterer')"
                  :class="[sortBy === 'Slaughterer' ? sortDirection : '']"
                >
                  Slaughterer
                </th>
                <th
                  @click="sort('Creditor')"
                  :class="[sortBy === 'Creditor' ? sortDirection : '']"
                >
                  Creditor
                </th>
                <th
                  @click="sort('Warmonger')"
                  :class="[sortBy === 'Warmonger' ? sortDirection : '']"
                >
                  Warmonger
                </th>
                <th
                  @click="sort('PowerBroker')"
                  :class="[sortBy === 'PowerBroker' ? sortDirection : '']"
                >
                  Power Broker
                </th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(teams, index) in sortedPlayer" :key="index">
                <td>{{ teams.name }}</td>
                <td>{{ teams.Dominator }}</td>
                <td>{{ teams.Slaughterer }}</td>
                <td>{{ teams.Creditor }}</td>
                <td>{{ teams.Warmonger }}</td>
                <td>{{ teams.PowerBroker }}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data: () => {
    return {
      player: [
        {
          name: "Player 1",
          Dominator: 2,
          Slaughterer: 10,
          Creditor: 3,
          Warmonger: 5,
          PowerBroker: 15
        },
        {
          name: "Player 2",
          Dominator: 2,
          Slaughterer: 3,
          Creditor: 5,
          Warmonger: 3,
          PowerBroker: 2
        },
        {
          name: "Player 3",
          Dominator: 2,
          Slaughterer: 9,
          Creditor: 5,
          Warmonger: 4,
          PowerBroker: 6
        },
        {
          name: "Player 4",
          Dominator: 2,
          Slaughterer: 5,
          Creditor: 6,
          Warmonger: 8,
          PowerBroker: 7
        },
        {
          name: "Player 5",
          Dominator: 2,
          Slaughterer: 4,
          Creditor: 8,
          Warmonger: 1,
          PowerBroker: 10
        }
      ],
      sortBy: "name",
      sortDirection: "asc",
      title: "Current Standing"
    };
  },
  methods: {
    sort: function(s) {
      if (s === this.sortBy) {
        this.sortDirection = this.sortDirection === "asc" ? "desc" : "asc";
      }
      this.sortBy = s;
    }
  },
  computed: {
    sortedPlayer: function() {
      // eslint-disable-next-line vue/no-side-effects-in-computed-properties
      return this.player.sort((p1, p2) => {
        let modifier = 1;
        if (this.sortDirection === "desc") modifier = -1;
        if (p1[this.sortBy] < p2[this.sortBy]) return -1 * modifier;
        if (p1[this.sortBy] > p2[this.sortBy]) return modifier;
        return 0;
      });
    }
  }
};
</script>
<style lang="scss" scoped>
body {
  background-color: grey;
}
#table {
  margin-bottom: 2em;
  display: block;
  overflow-x: auto;
  white-space: nowrap;
  h1 {
    color: #0438ad;
    padding: 1.5rem 0;
    text-align: center;
    font-weight: 900;
  }
  table {
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.12), 0 2px 2px rgba(0, 0, 0, 0.12),
      0 4px 4px rgba(0, 0, 0, 0.12), 0 8px 8px rgba(0, 0, 0, 0.12),
      0 16px 16px rgba(0, 0, 0, 0.12);
    background: #ffffff;

    tr {
      transition: ease-in-out;
      &:nth-of-type(even) {
        background: #ececec;
      }
      &:hover,
      &:active {
        background: #066d04;
        border: 2px solid #066d04;
        &th:hover,
        &th:active {
          color: #ffffff;
        }
      }
      th {
        font-size: 1.2em;
        color: #fff;
        line-height: 1.4;
        text-transform: uppercase;
        background-color: #36304a;
        max-width: 100%;
        width: 20em;
        padding: 0.5em 1em;
        cursor: pointer;
        &:active {
          cursor: progress;
        }

        &:hover {
          background-color: #484848 !important;
        }
      }
      td {
        font-size: 1.2em;
        color: #808080;
        line-height: 1.4;
        padding: 1em 0;
        &:hover,
        &:active {
          color: #ffffff;
        }
      }
    }
  }
}
</style>
